(function() {
  'use strict';

  angular.module('angularChallenge', ['ngRoute']);

})();

(function() {
  'use strict';

  angular
    .module('angularChallenge')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController() {
    var vm = this;
    vm.title = "Welcome to the AngularJS Code Challenge";
    vm.description = "This is the boilerplate project for the Angular JS challenge";
  }
})();

(function() {
  'use strict';

  angular
    .module('angularChallenge')
    .config(routeConfig);

  function routeConfig($routeProvider) {
    $routeProvider
      .when('/', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })
      .otherwise({
        redirectTo: '/'
      });
  }

})();

angular.module("angularChallenge").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<div class=\"container\"><div class=\"intro\"><a href=\"https://www.savedo.de\"><img src=\"/assets/images/savedo.svg\"></a><h1 ng-bind=\"vm.title\"></h1><p class=\"lead\" ng-bind=\"vm.description\"></p></div></div>");}]);